/* Styles par défaut pour les tailles d'écran grandes */
.Heading {
  font-size: 3rem;
  margin-bottom: 0;
  animation: fadeInUp 0.5s ease-out forwards;
}

.Subheading {
  font-size: 1.7rem;
  margin-top: 0;
  animation: fadeInUp 0.5s ease-out 0.2s forwards;
}

.Body1 {
  font-size: 1.5rem;
  animation: fadeInUp 0.5s ease-out 0.4s forwards;
}

.Body2 {
  font-size: 1.25rem;
  animation: fadeInUp 0.5s ease-out 0.5s forwards;
}



/* Styles pour les écrans de taille moyenne */
@media (max-width: 1280px) {
  .Heading {
    font-size: 2.8rem;
  }

  .Subheading {
    font-size: 1.6rem;
  }

  .Body1 {
    font-size: 1.375rem;
  }

  .Body2 {
    font-size: 1.125rem;
  }
}

/* Styles pour les écrans de taille petite */
@media (max-width: 960px) {
  .Heading {
    font-size: 2.7rem;
  }

  .Subheading {
    font-size: 1.5rem;
  }

  .Body1 {
    font-size: 1.25rem;
  }

  .Body2 {
    font-size: 1rem;
  }
}

/* Styles pour les très petits écrans (mobiles) */
@media (max-width: 600px) {
  .Heading {
    font-size: 2rem;
  }

  .Subheading {
    font-size: 1.5rem;
  }

  .Body1 {
    font-size: 1rem;
  }

  .Body2 {
    font-size: 0.875rem;
  }
}

.full-width-circle-image {
  clip-path: ellipse(50% 40%);
  height: 30vh;
  animation: fadeInUp 0.5s ease-out 0.1s forwards;
  /* Prend toute la hauteur de la fenêtre */
}

/* src/styles/animations.css */

/* Définition des animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
